import React from 'react';
import s from './Post.module.css';

function Post(props) {
    return (        
        <div className={s.item}>
            <img src="https://avatars.mds.yandex.net/get-pdb/2269311/6e8a6e40-91b1-4936-8dba-21506ee1f723/s1200?webp=false" alt=""/>
            {props.message} <br/>
            likes:{props.likesCount}
        </div>   
    );
};
export default Post;