import {rerenderEntireTree} from "../render";



let state={
    profilePage:{
        posts:[
            {id:1, message:'dfojsjdkos',likesCount:0},
            {id:2, message:'dfojsjdkos',likesCount:0},
            {id:3, message:'dfojsjdkos',likesCount:0},
            {id:4, message:'dfojsjdkos',likesCount:0},
            {id:5, message:'dfojsjdkos',likesCount:0},
            {id:6, message:'dfojsjdkos',likesCount:0},
        ],
        newPostText:''
    },
    dialogsPage:{
        dialogs:[
            {id: '1', name: 'Dimych'},
            {id: '2', name: 'Andrew'},
            {id: '3', name: 'Vasya'},
            {id: '4', name: 'Chel'},
            {id: '5', name: 'echo'},
        ],
        messages:[
            {id: '1', message: 'Hi ksta'},
            {id: '2', message: 'smert'},
            {id: '3', message: 'ban'},

        ]
    }


}

window.state=state

export let addSymbol=(symbol)=>{
    state.profilePage.newPostText= symbol;

    rerenderEntireTree(state);
}

export let addPost = ()=>{
    let newPost={
        id:5,
        message:state.profilePage.newPostText,
        likesCount:0
    };
    if(state.profilePage.newPostText.length<94){
        state.profilePage.posts.push(newPost);
        state.profilePage.newPostText='';
    }
    rerenderEntireTree(state);

}

export default state